import { login } from '@/pages/api/request.js'
export default{
	state:{
		url:'http://daranzhibojiekou.daranjituan.com', //
		loginStatus:false, //
		orderStatus:0, // 0  1  2 
		userMobile:'', //
		isVip:true, //vip
		authenticate:true, //
		token:'',
		referral_code:'',
		carNum:'',
		userInfo:'', //
		nickName:''
	},
	// 
	getters:{
		
	},
	// 
	mutations:{
		// openid
		getopenid(state){
			wx.login({
			  success(res) {
				var code = res.code
				let treferral_code;
				let r = uni.getStorageSync('referral_code')
				if(r == '' || r == 'undefind' || r == null){
					treferral_code = ''
				}else{
					treferral_code = uni.getStorageSync('referral_code')
				}
				// 
				let params ={
					code:code,
					referral_code:treferral_code
				}
				// 
				login(params).then(
				    (res) => {
						// 
						if(res.data.code === 200){
							state.token = res.data.data.token
							state.referral_code = res.data.data.referral_code
							state.loginState = true
							state.userMobile = res.data.data.phone
								if(res.data.data.is_member === 1){
									state.isVip = true
								}else{
									state.isVip = false
								}
								uni.removeStorageSync('referral_code')
							}else{
							uni.showToast({
								title:'',
								icon:'loading'
							})
						}
				    },
				    (err) => {
				        uni.showToast({
				        	title:'',
				        	icon:'loading'
				        })
				    }
				)
			  }
			})
		},
	},
	// 
	actions:{
	
	}
}